<template>
  <div class="parentComponent">
    <UploadImage :options="options" @uploads-change="uploadsChange"></UploadImage>
  </div>
</template>

<script>
import UploadImage from '@/plug/uploadImage'
export default {
  name: 'parentComponent',
  data () {
    return {
      uploadFormVal: null,
      options: {
        isBase64: true, // base64falseBlob
        maxLength: 9, // 
        url: '', // 
        compressWidth: 900, // 
        maxsize: 100 * 1024, // 1M
        sendData: { // iduploadDatabase64
          fileName: '', // 
          folder: 'report', // 
          id: '',
          uploadData: ''
        }
      }
    }
  },
  components: {
    UploadImage
  },
  methods: {
      // 
    uploadsChange (val) {
      this.uploadFormVal = val
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.parentComponent{
  font-size: .4rem;
}
</style>



