<template>
  <div class="father_the_son">
    <h3 class="hea"></h3>
    <div class="lists">---{{lists}}</div>
    <div>---{{msg}}</div>
  </div>
</template>

<script>
import Bus from "@/assets/js/bus.js";
export default {
  name: "",
  data() {
    return {
      msg: ""
    };
  },
  props: {
    lists: {
      // type:[String, Number,Array,Boolean,Object,Function,Symbol],//
      type: null //null//
    }
  },
  components: {},

  computed: {},

  beforeMount() {},

  mounted() {
    let _this = this;
    Bus.$on("msg", e => {
      _this.msg = e;
      console.log(`${e}`);
    });
  },

  methods: {},

  watch: {}
};
</script>
<style lang='scss' scoped>
.father_the_son {
    > div {
        widows: 100%;
        padding:10px;
        border-bottom: 1px solid red;
    }
}
</style>


